body {
	font-family: 'Rubik', serif;
	background: #e0eafc; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#cfdef3,
		#e0eafc
	); /* Chrome 10-25, Safari 5.1-6 */
